.banner{
   
    background-size: cover;
    height:450px;
    color: white;
}
.content{
    padding-top: 100px;
    padding-left: 45px;
    height: 190px;
}
.title{
    font-size: 4rem;
    font-weight: 800;
    padding-top: 1rem;
    padding-bottom: .3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color:black;
    background-color: #e6e6e6;
}
.description{
    width:45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}

.fade{
    height: 10rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}